// models/Lead.js
import mongoose from 'mongoose';

const leadSchema = new mongoose.Schema({
  // Personal Information
  firstName: { type: String, required: true },
  lastName: { type: String, required: true },
  email: { type: String, required: true },
  phone: { type: String, required: true },
  
  // Date of Birth
  dobMonth: { type: Number, required: true },
  dobDay: { type: Number, required: true },
  dobYear: { type: Number, required: true },
  
  // Address
  address1: { type: String, required: true },
  city: { type: String, required: true },
  state: { type: String, required: true },
  zipCode: { type: String, required: true },
  monthsAtAddress: { type: Number, required: true },
  residenceType: { type: String, required: true }, // Own/Rent
  
  // Financial
  requestedAmount: { type: Number, required: true },
  ssn: { type: String, required: true },
  monthlyIncome: { type: Number, required: true },
  incomeType: { type: String, required: true },
  
  // Employment
  employerName: String,
  monthsEmployed: Number,
  payFrequency: String,
  nextPayDate: Date,
  directDeposit: Boolean,
  
  // Banking
  bankName: String,
  bankABA: String,
  bankAccountNumber: String,
  bankAccountType: String,
  debitCard: Boolean,
  monthsAtBank: Number,
  
  // Drivers License
  driversLicense: String,
  driversLicenseState: String,
  
  // Additional
  bestTimeToCall: String,
  loanPurpose: String,
  creditRating: String,
  ownHome: Boolean,
  ownCar: Boolean,
  activeMilitary: Boolean,
  
  // Tracking
  clientIP: String,
  clientUserAgent: String,
  trackingId: String,
  note: String,

  // Klaviyo Integration
  klaviyo_status: { type: Number, default: 0 },
  
  // Metadata
  createdAt: { type: Date, default: Date.now },
  updatedAt: { type: Date, default: Date.now }
});

export default mongoose.models.Lead || mongoose.model('Lead', leadSchema);